import React, { useState, useEffect } from 'react';
import Box from '@mui/material/Box';
import Dialog from '@mui/material/Dialog';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import ViewInArIcon from '@mui/icons-material/ViewInAr';
import Typography from '@mui/material/Typography';

export default function ModalInstructions() {
    const [open, setOpen] = useState(true);

    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('md'));

    const handleClose = () => {
        setOpen(false);
    };
    const openModal = (e) => {
        setOpen(true);
    }

    useEffect(() => {
        document.addEventListener('close_instructions_modal', handleClose)
        return () => document.removeEventListener("close_instructions_modal", handleClose);
    }, []);


    return (
        <Dialog
            fullScreen={false}
            open={open}
            aria-labelledby="responsive-dialog-title"
            PaperProps={{
                style: {
                    backgroundColor: 'transparent',
                    boxShadow: 'none',
                },
            }}
        >
            
            <img className="o-logo-header"
                        src={process.env.PUBLIC_URL + '/assets/targets/Poster1.jpg'}
                        height="80%" alt=""
                        style={{ marginBottom: '1rem', objectFit:'contain', opacity:0.3 }} />
            <Box
                sx={{
                    width: "100%",
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center',
                    alignItems: 'center',
                    color: 'white',
                    textAlign:'center'
                }}
            >
                
                {/* <ViewInArIcon sx={{ fontSize: '7rem', mb:3, position: 'absolute', top:'50%', left:'50%' }} /> */}

                <Typography variant="button" display="block" gutterBottom sx={{textAlign:'center'}}>
                    Acerca la cámara a la página principal del póster "Por la senda del Chaitán"
                </Typography>
            </Box>


        </Dialog>
    );
}